/*************************************************************************************************/
/*************************************************************************************************/
.main-logo {
  max-width: 100vw; }

/*************************************************************************************************/
/*************************************************************************************************/
.portfolio {
  vertical-align: top;
  text-align: center; }

.portfolio-card {
  display: inline-block;
  vertical-align: top;
  max-width: 350px;
  margin: 0.5rem; }

.portfolio-card .card-top-half {
  min-height: 301px; }

.portfolio-card .img-container {
  text-align: center;
  min-height: 150px;
  vertical-align: top; }

.portfolio-card .card-img-top {
  width: auto;
  max-width: 80%;
  max-height: 150px;
  vertical-align: middle; }

.portfolio-card .technology-list .technology-chip {
  margin-top: 0.5rem;
  display: inline-block; }

.portfolio-card .technology-list .technology-chip img {
  max-width: 50px;
  margin: 0.3rem 0.2rem; }

.portfolio-card .list-group-item {
  padding: 0; }

.portfolio-card .important-links {
  position: relative;
  min-height: 50px; }

.portfolio-card .see-live {
  margin-right: 2rem; }

.portfolio-card .github-repo {
  max-width: 60px; }

.portfolio-card.react-playground .card-img-top, .portfolio-card.redux-counter .card-img-top, .portfolio-card.react-todo-1 .card-img-top,
.portfolio-card.react-redux-todo .card-img-top {
  width: 80%;
  padding-top: 1.5rem; }

.portfolio-card.react-playground .card-img-top {
  padding-top: 1rem; }

.portfolio-card.react-redux-todo .card-img-top {
  padding-top: 2rem; }

@media screen and (min-width: 650px) {
  .portfolio-card {
    width: 45%; } }

@media screen and (min-width: 900px) {
  .portfolio-card {
    width: 31%;
    max-width: 325px; } }



